.contact-container{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    padding: 11rem 0;
    height: 20vh;
}
.contact-title{
    font-size: 30px;
    margin-bottom: 20px;
    
    color: rgb(30, 69, 70);
}
.contact-msg{
    font-size: 20px;
    margin-bottom: 2rem;
}
.content{
    display:flex;
    align-items: center;
    gap: 4rem;
}
.info-box{
    display:flex;
    align-items: center;
    gap:1.5rem;
}
.contact-container i{
    display: flex;
    flex-wrap: wrap;
    font-size: 2rem ;
}

 .icons{
    background-color: white;
    padding: 1rem;
    box-shadow: 0px 0px 10px rgb(0 0 0 / 10%);
    border-radius: 50%;
}
.info-text{
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

@media only screen and (max-width: 1201px) {
    .contact-content{
        width: 50%;    
        
    }
    .info-box{
        margin: 1rem;
    }
    .contact-msg{
        font-size: 20px;
        margin-bottom: 1rem;
    }
  }